import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import information from '@/pages/information'
import informationDetail from '@/pages/informationDetail'
import addPost from '@/pages/addPost'

import shopInfo from '@/pages/index/shopInfo'
import transferred from '@/pages/index/transferred'
import transferredDetail from '@/pages/index/transferredDetail'

import User from '@/pages/user/user'
import collection from '@/pages/user/collection'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/information',
      name: 'information',
      component: information
    },
    {
      path: '/post/add',
      name: 'addPost',
      component: addPost
    },
    {
      path: '/information/:id',
      name: 'informationDetail',
      component: informationDetail
    },
    {
      path: '/user/user',
      name: 'User',
      component: User
    },
    {
      path: '/user/collection',
      name: 'collection',
      component: collection
    },
    {
      path: '/index/shopInfo',
      name: 'shopInfo',
      component: shopInfo
    },
    {
      path: '/index/transferred',
      name: 'transferred',
      component: transferred
    },
    {
      path: '/index/transferredDetail',
      name: 'transferredDetail',
      component: transferredDetail
    }
    
  ]
})



